import React, {useState, useEffect} from 'react';

export const Func = ()=>{
    const [show, setShow] = useState(false);
    const [post, setPost] = useState(null);

    const [error, setError] = useState(null)

    useEffect(()=>{
        async function getNews() {
            await fetch('https://jsonplaceholder.typicode.com/posts/1')
                .then(res => res.json())
                .then(
                    (result)=>{
                        setPost(result)
                    },
                    (error)=>{
                        setError(error)
                        console.log(error)
                    }
                )
        }
        getNews();
    }, [show])

    let handleClick =()=>{
        setShow(true)
    }

    let handleDiv = (event)=>{
        const btnn = document.getElementById('btn');
        console.log(btnn);
    }

    if(!show){
        return(
            <button id="btn" onClick={handleClick} name="click">Click</button>
        )
    }

    for (const key in post) {
        const element = post[key];
        console.log(element);
    }

    return(
        <>
            <div 
                onClick={handleDiv}
                style={
                {
                    width: 200,
                    height: 200,
                    background: 'white',

                }
                }>
                Simple Message
                <h2>{post.id}</h2>
                <p>{post.title}</p>
                </div>
                
            <button id="btn" onClick={handleClick} name="click">Click</button>
        </>
    )
}